<template>
  <div class="login">
    <h1>login</h1>
    <el-input v-model="input" placeholder="输入：1正确，0错误" class="login-input"></el-input>
    <el-button @click="login" class="login-button">登录</el-button>
  </div>
</template>

<style lang="scss" scoped>
.login {
  width: 100%;
  min-height: 500px;
  text-align: center;

  &-input {
    width: 300px;
    margin-right: 20px;
  }

  &-button {
    width: 300px;
  }
}
</style>

<script>
export default {

  name: 'Login',
  data () {
    return {
      input: '',
    }
  },
  methods: {
    login () {
      if (this.input === '1') {
        this.$$jsCookie.set('userId', '1')
        this.$$jsCookie.set('userToken', Date.now())

        const isProd = process.env.NODE_ENV === 'production'

        const baseUrl = isProd ? '/awesome/cms-admin/#' : ''

        const url = window.location.search
        const query = this.$$queryString.parse(url)
        const { referer } = query
        const href = referer ? decodeURIComponent(referer) : `${baseUrl}/orderList`

        window.location.replace(href)
      } else {
        this.$alert('login fail')
      }
    },
  },

}
</script>
